import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { API } from './api';
import * as jwt_decode from 'jwt-decode';
import { Token } from '../models/token.model';

const LOGIN_URL = '/management-service/users/login';
const USER_INFO = '/management-service/user-informations';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

constructor(private router: Router,
  private api: API) { }

  login(data) {
    return this.api.post(LOGIN_URL, data);
  }

  logout() {
    this.removeAllLocalStorage();
    this.router.navigate(['/login']);
  }

  getUserInfo(id) { 
    const url = USER_INFO + `/${id}`;
    return this.api.get(url);
  }

  setPartyId(partyId) {
    return localStorage.setItem('partyId', partyId);
  }

  get partyId() {
    return localStorage.getItem('partyId');
  }

  setToken(token) {
    return localStorage.setItem('token', token);
  }

  getToken() {
    return localStorage.getItem('token');
  }

  removeAllLocalStorage() {
    localStorage.removeItem('token');
  }

  jwtDecode(token) {
    var decoded = jwt_decode(token);
    return new Token(decoded);
  }
}
