import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { HttpParams, HttpClient, HttpHeaders, HttpErrorResponse, HttpResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError, retry } from 'rxjs/operators';
import { Config } from 'src/app/config';

const config_responseType = {
  'blob': 'blob',
  'arraybuffer': 'arraybuffer',
  'json': 'json',
  'text': 'text',
  'none': ''
};

@Injectable()
export class API {
  private config = Config;

  constructor(
    private http: HttpClient,
    public router: Router
  ) { }

  /**
   * Get method
   * @param url 
   */
  get(url) {
    return this.http.get(this.config.API_URL + url, this.getRequestOptions())
    .pipe(
      catchError(this.handleError)
    );
  }

  /**
   * Post method
   * @param url 
   * @param data 
   */
  post(url, data) {
    data = JSON.stringify(data);
    return this.http.post(this.config.API_URL + url, data, this.getRequestOptions())
    .pipe(
      catchError(this.handleError)
    );
  }

  /**
   * POST multipath
   * @param url 
   * @param data 
   */
  postMultiPath(url, data: FormData) {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'multipart/form-data; boundary=',
        'Authorization': `Bearer ${localStorage.getItem('token') || ""}`
      })
    };
    return this.http.post(this.config.API_URL + url, data, httpOptions)
    .pipe(
      catchError(this.handleError)
    );
  }

  /**
   * Put method
   * @param url 
   * @param data 
   */
  put(url, data) {
    data = JSON.stringify(data);
    return this.http.put(this.config.API_URL + url, data, this.getRequestOptions())
    .pipe(
      catchError(this.handleError)
    )
  }

  /**
   * Delete method
   * @param url
   */
  delete(url) {
    return this.http.delete(this.config.API_URL + url, this.getRequestOptions())
    .pipe(
      catchError(this.handleError)
    )
  }

  /**
   * Download file
   * @param url 
   */
  download(url) {
    return this.http.get(this.config.API_URL + url, this.getDownloadOptions())
    .pipe(
      catchError(this.handleError)
    )
  }

  /**
   * Make request options
   */
  getRequestOptions() {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json',
        'Authorization': `Bearer ${localStorage.getItem('token') || ""}`
      })
    };

    return httpOptions;
  }

  /**
   * Http options for download and preview
   */
  getDownloadOptions() {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json',
        'Authorization': `Bearer ${localStorage.getItem('token') || ""}`
      }),
      responseType: 'blob' as 'text'
    };

    return httpOptions;
  }

  /**
   * Handle Error
   * @param error 
   */
  private handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', error.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${error.status}, ` +
        `body was: ${error.error}`);
    }
    // return an observable with a user-facing error message
    return throwError(
      'Something bad happened; please try again later.');
  };
}