import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { API } from './api';

const LIST_CUSTOMER_URL = '/management-service/parties/list';
const CREATE_CUSTOMER_URL = '/management-service/parties/create-new-customer';

@Injectable({
  providedIn: 'root'
})
export class CustomerService {

constructor(private router: Router,
  private api: API) { }

  /**
   * Get List Customer
   * @param page 
   * @param maxSize 
   * @param propertiesSort 
   * @param sort 
   */
  getListCustomers(page, maxSize, propertiesSort, sort) {
    const url = LIST_CUSTOMER_URL + `?page=${page}&maxSize=${maxSize}&propertiesSort=${propertiesSort}&sort=${sort}`;
    return this.api.get(url);
  }

  /**
   * Create customer
   * @param data 
   */
  createCustomer(data) {
    return this.api.post(CREATE_CUSTOMER_URL, data);
  }
}
