import { NewEnterpriseCustomerComponent } from './new-enterprise-customer/new-enterprise-customer.component';
import { Component, OnInit, ViewChild } from '@angular/core';


@Component({
  selector: 'app-customer-create',
  templateUrl: './customer-create.component.html',
  styleUrls: ['./customer-create.component.scss']
})
export class CustomerCreateComponent implements OnInit {
  @ViewChild(NewEnterpriseCustomerComponent ) child: NewEnterpriseCustomerComponent ; 
  type = 1;

  constructor() { }

  ngOnInit(): void {
  }

  choose(id){
    this.type = id;
  }

  submit() {
    this.child.createCustomer();
  }
}
