import { API } from './api';
import { Injectable } from '@angular/core';

const LIST_CONTRACT_URL = '/management-service/contracts/list';

@Injectable({
  providedIn: 'root'
})
export class ContractService {

  constructor(private api: API) { }

  getAllKH() {
    
  }

  /**
   * GET list contract
   * @param page 
   * @param maxSize 
   * @param propertiesSort 
   * @param sort 
   */
  getListContract(page, maxSize, propertiesSort, sort) {
    const url = LIST_CONTRACT_URL + `?page=${page}&maxSize=${maxSize}&propertiesSort=${propertiesSort}&sort=${sort}`;
    return this.api.get(url);
  }
}
