export class ListContractItem {
    contractNumber: string;
    title: string;
    type: string;
    currentStage: string;
    modified: string;

    constructor(obj = null) {
        obj = obj || '';
        this.contractNumber = obj.contractNumber || '';
        this.title = obj.title || '';
        this.type = obj.type || '';
        this.currentStage = obj.currentStage || '';
        this.modified = obj.modified || '';
    }
}