export class CreeatCustomerParam {
    daiDien: DaiDien;
    toChuc: ToChuc;

    constructor(obj = null) {
        obj = obj || {};
        this.daiDien = new DaiDien(obj.daiDien);
        this.toChuc = new ToChuc(obj.toChuc);
    }
}

export class ToChuc {
    tenToChuc: string;
    soDkdn: string;
    ngayCapSoDkdn: string;
    noiCapSoDkdn: string;
    diaChiThanhToan: string;
    diaChiTruSo: string;
    maSoThue: string;
    soTaiKhoanGiaoDich: string;
    nganHangGiaoDich: string;
    sdt: string;
    email: string;

    constructor(obj = null) {
        obj = obj || {};
        this.tenToChuc = obj.tenToChuc || '';
        this.soDkdn = obj.soDkdn || '';
        this.ngayCapSoDkdn = this.convertStringToDate(obj.ngayCapSoDkdn) || '';
        this.noiCapSoDkdn = obj.noiCapSoDkdn || '';
        this.diaChiThanhToan = obj.diaChiThanhToan || '';
        this.diaChiTruSo = obj.diaChiTruSo || '';
        this.maSoThue = obj.maSoThue || '';
        this.soTaiKhoanGiaoDich = obj.soTaiKhoanGiaoDich || '';
        this.nganHangGiaoDich = obj.nganHangGiaoDich || '';
        this.email = obj.email || '';
        this.sdt = obj.sdt || '';
    }

    convertStringToDate(str: string) {
        if(str !== '' && str !== null && str !== undefined) {
            var values = str.split('/');
            if(values.length >= 3) {
                return `${values[2]}-${values[1]}-${values[0]}T00:00:00+07:00`;
            } else {
                return null;
            }
        } else {
            return null;
        }
    }
}

export class DaiDien {
    ten: string;
    chucVu: string;
    cmnd: string;
    ngayCap: string;
    noiCap: string;
    ngaySinh: string;
    gioiTinh: string;
    soNha: string;
    duong: string;
    thanhPho: string;
    diaChiThuongTru: string;
    email: string;
    sdt: string;

    constructor(obj = null) {
        obj = obj || {};
        this.ten = obj.ten || '';
        this.chucVu = obj.chucVu || '';
        this.cmnd = obj.cmnd || '';
        this.ngayCap = this.convertStringToDate(obj.ngayCap) || '';
        this.noiCap = obj.noiCap || '';
        this.ngaySinh = this.convertStringToDate(obj.ngaySinh) || '';
        this.gioiTinh = obj.gioiTinh || '';
        this.soNha = obj.soNha || '';
        this.duong = obj.duong || '';
        this.thanhPho = obj.thanhPho || '';
        this.diaChiThuongTru = obj.diaChiThuongTru || '';
        this.email = obj.email || '';
        this.sdt = obj.sdt || '';
    }

    convertStringToDate(str: string) {
        if(str !== '' && str !== null && str !== undefined) {
            var values = str.split('/');
            if(values.length >= 3) {
                return `${values[2]}-${values[1]}-${values[0]}T00:00:00+07:00`;
            } else {
                return null;
            }
        } else {
            return null;
        }
    }
}