export class ListCustomerItem {
    userId: string;
    partyId: string;
    partyNumber: string;
    tenToChuc: string;
    type: string;
    diaChiTruSo: string;
    maSoThue: string;
    disable: string;
    tenDaiDien: string;

    constructor(obj = null) {
        obj = obj || {};
        this.userId = obj.userId || '';
        this.partyId = obj.partyId || '';
        this.partyNumber = obj.partyNumber || '';
        this.tenToChuc = obj.tenToChuc || '';
        this.type = obj.type || '';
        this.diaChiTruSo = obj.diaChiTruSo || '';
        this.maSoThue = obj.maSoThue || '';
        this.disable = obj.disable || '';
        this.tenDaiDien = obj.tenDaiDien || '';
    }
}