import { ListContractItem } from './../../../utils/models/contract/list-contract/list-contract-item.model';
import { ListContractResponse } from './../../../utils/models/contract/list-contract/list-contract-response.model';
import { Component, OnInit } from '@angular/core';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { ContractService } from 'src/app/utils/services/contract.service';

declare var pdfViewer: any;

@Component({
  selector: 'app-contract-list',
  templateUrl: './contract-list.component.html',
  styleUrls: ['./contract-list.component.scss']
})
export class ContractListComponent implements OnInit {

  page = 1;
  maxSize = 10;
  propertiesSort = 'contractNumber';
  sort = 'ASC';
  totalElement = 0;

  response = new ListContractResponse();
  list: ListContractItem[] = [];

  constructor(
    private contractService: ContractService
  ) { }

  filePdf: File = null;

  ngOnInit(): void {
    this.getListContract();
  }

  getListContract() {
    this.contractService.getListContract(this.page, this.maxSize, this.propertiesSort, this.sort).subscribe((res) => {
      this.response = new ListContractResponse(res);
      console.log(this.response);
      this.list = this.response.object.data;
      this.page = this.response.object.page;
      this.maxSize = this.response.object.maxSize;
      this.propertiesSort = this.response.object.propertiesSort;
      this.sort = this.response.object.sort;
      this.totalElement = this.response.object.totalElement;
    }, err => {

    }, () => {

    });
  }

  loadPage(page) {
    if (this.page !== page) {
      this.page = page;
      this.getListContract();
    }
  }

  handleFileInput(files: FileList) {
    this.filePdf = files.item(0);
    pdfViewer.initData(this.filePdf);

    this.previewPdf();
  }

  previewPdf() {
    pdfViewer.start();
  }

}
