import { ListTemplateResponse } from './../../../utils/models/template/list-template/list-template-response.model';
import { ListTemplateItem } from './../../../utils/models/template/list-template/list-template-item.model';
import { TemplateService } from './../../../utils/services/template.service';
import { Component, OnInit } from '@angular/core';
import { AuthService } from 'src/app/utils/services/auth.service';

@Component({
  selector: 'app-contract-template-list',
  templateUrl: './contract-template-list.component.html',
  styleUrls: ['./contract-template-list.component.scss']
})
export class ContractTemplateListComponent implements OnInit {

  partyId = '';
  page = 1;
  maxSize = 10;
  propertiesSort = 'name';
  sort = 'ASC';
  totalElement = 0;

  response = new ListTemplateResponse();
  list: ListTemplateItem[] = [];

  constructor(
    private templateService: TemplateService,
    private auth: AuthService
  ) { 
    this.partyId = auth.partyId;
  }

  ngOnInit(): void {
    this.getListTemplate();
  }

  getListTemplate() {
    this.templateService.getListTemplate(this.partyId, this.page, this.maxSize, this.propertiesSort, this.sort).subscribe((res) => {
      this.response = new ListTemplateResponse(res);
      this.list = this.response.object.data;
      this.page = this.response.object.page;
      this.maxSize = this.response.object.maxSize;
      this.propertiesSort = this.response.object.propertiesSort;
      this.sort = this.response.object.sort;
      this.totalElement = this.response.object.totalElement;
    }, err => {

    }, () => {

    })
  }

  loadPage(page) {
    if (this.page !== page) {
      this.page = page;
      this.getListTemplate();
    }
  }
}
