import { CreateTemplateResponse } from './../../../utils/models/contract/create-template/create-template-response.model';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { TemplateService } from './../../../utils/services/template.service';
import { Component, OnInit } from '@angular/core';

declare var $:any;
declare var pdfViewer: any;
@Component({
  selector: 'app-contract-template-create',
  templateUrl: './contract-template-create.component.html',
  styleUrls: ['./contract-template-create.component.scss']
})
export class ContractTemplateCreateComponent implements OnInit {
  groupId = '5e7316f548baea872e6e0039';
  file: File = null;
  fileList: FileList;
  formCreate: FormGroup;

  createResponse = new CreateTemplateResponse();
  idTemplate = '';
  inputFileLabel = 'Chn file';

  constructor(
    private templateService: TemplateService,
    private fb: FormBuilder
  ) { }

  ngOnInit(): void {
    this.formCreate = this.fb.group({
      name: new FormControl('', Validators.required),
      type: new FormControl('', Validators.required)
    })

    pdfViewer.initPlugin();
  }

  get name() { return this.formCreate.get('name') }
  get type() { return this.formCreate.get('type') }

  previewTemplate() {
    let formData = new FormData();
    formData.append('file', this.file)
    console.log(this.file);
    this.templateService.previewTemplate(formData).subscribe((res) => {
      console.log(res);
    }, err => {

    }, () => {

    });
  }

  uploadTemplate() {
    let formData: FormData = new FormData();
    formData.append('file', this.file)
    this.templateService.postUploadTempate(this.idTemplate, formData).subscribe((res) => {
      console.log(res);
    })
  }

  postCreateTemplate() {
    this.templateService.postCreateTemplate(this.groupId, this.formCreate.value).subscribe((res) => {
      this.createResponse = new CreateTemplateResponse(res);
      console.log(this.createResponse);
      this.idTemplate = this.createResponse.object.id;
    }, err => {

    }, () => {
      this.uploadTemplate();
    })
  }

  //MARK: Listening event
  handleFileInput(e) {
    this.file = e.target.files[0] || null;
    if (this.file && this.file !== null && this.file !== undefined) {
      this.inputFileLabel = this.file.name
      this.previewTemplate();
    } else {
      this.inputFileLabel = 'Chn file';
    }
  }

  submitTemplate() {
    if (this.formCreate.valid && this.file !== null) {
      this.postCreateTemplate();
    }
  }

  previewPdf() {
    pdfViewer.start();
  }
}
