import { Component, OnInit } from '@angular/core';

declare var $:any;
declare var pdfViewer: any;
@Component({
  selector: 'app-contract-create',
  templateUrl: './contract-create.component.html',
  styleUrls: ['./contract-create.component.scss']
})
export class ContractCreateComponent implements OnInit {
  maxStep = 5;
  currentStep = 1;
  backStep = 0;
  filePdf: File = null;

  constructor() { }

  ngOnInit(): void {
    this.initStepView();
    pdfViewer.initPlugin();
  }

  handleFileInput(files: FileList) {
    this.filePdf = files.item(0);
    pdfViewer.initData(this.filePdf);

    this.previewPdf();
  }

  previewPdf() {
    pdfViewer.start();
  }

  initStepView() {
    $("[id^='step']").hide();
    $(`#step${this.currentStep}`).show();
  }

  controlStep(action) {
    switch (action) {
      case "next":
        if(this.currentStep < this.maxStep) {
          this.backStep = this.currentStep;
          this.currentStep += 1;
          $(`#step${this.backStep}`).hide();
          $(`#step${this.currentStep}`).show();
        }
        break;
      case "back":
        if(this.currentStep > 0) {
          $(`#step${this.currentStep}`).hide();
          $(`#step${this.backStep}`).show();
          this.currentStep -= 1;
          this.backStep - 1 > 0 ? this.backStep -= 1 : this.backStep = 0;
        }
        break;
    }
  }

}
