import { ContractTemplateCreateComponent } from './pages/contract-template-manager/contract-template-create/contract-template-create.component';
import { ContractTemplateListComponent } from './pages/contract-template-manager/contract-template-list/contract-template-list.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DashboardComponent } from './pages/dashboard/dashboard.component';
import { BlankComponent } from './pages/blank/blank.component';
import { MainComponent } from './layout/main/main.component';
import { AuthGuard } from './utils/guards/auth.guard';
import { WrongRouteComponent } from './pages/wrong-route/wrong-route.component';
import { LoginComponent } from './pages/login/login.component';
import { NonAuthGuard } from './utils/guards/non-auth.guard';
import { ContractListComponent } from './pages/contract-manager/contract-list/contract-list.component';
import { ContractCreateComponent } from './pages/contract-manager/contract-create/contract-create.component';
import { CustomerListComponent } from './pages/customer-manager/customer-list/customer-list.component';
import { CustomerCreateComponent } from './pages/customer-manager/customer-create/customer-create.component';
import { ContractDetailComponent } from './pages/contract-manager/contract-detail/contract-detail.component';
import { InternalInfoComponent } from './pages/internal-manager/internal-info/internal-info.component';
import { InternalReportComponent } from './pages/internal-manager/internal-report/internal-report.component';
import { ContractSignComponent } from './pages/contract-manager/contract-sign/contract-sign.component';
import { InternalInfoUpdateComponent } from './pages/internal-manager/internal-info-update/internal-info-update.component';


const routes: Routes = [
  {
    path: 'portal',
    component: MainComponent,
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    children: [
      // {
      //   path: 'profile',
      //   component: ProfileComponent
      // },
      {
        path: 'blank',
        component: BlankComponent
      },
      {
        path: 'dashboard',
        component: DashboardComponent,
        data: { roles: ['user'] }
      },
      {
        path: 'contracts/list',
        component: ContractListComponent,
        data: { roles: ['user'] }
      },
      {
        path: 'contracts/create',
        component: ContractCreateComponent,
        data: { roles: ['user'] }
      },
      {
        path: 'contracts/detail/:id',
        component: ContractDetailComponent,
        data: { roles: ['user'] }
      },
      {
        path: 'contracts/sign/:id',
        component: ContractSignComponent,
        data: { roles: ['user'] }
      },
      {
        path: 'contract-templates/list',
        component: ContractTemplateListComponent,
        data: { roles: ['user'] }
      },
      {
        path: 'contract-templates/create',
        component: ContractTemplateCreateComponent,
        data: { roles: ['user'] }
      },
      {
        path: 'customers/list',
        component: CustomerListComponent,
        data: { roles: ['user'] }
      },
      {
        path: 'customers/create',
        component: CustomerCreateComponent,
        data: { roles: ['user'] }
      },
      {
        path: 'internal/info',
        component: InternalInfoComponent,
        data: { roles: ['user'] }
      },
      {
        path: 'internal/info-update',
        component: InternalInfoUpdateComponent,
        data: { roles: ['user'] }
      },
      {
        path: 'internal/report',
        component: InternalReportComponent,
        data: { roles: ['user'] }
      },
      {
        path: '',
        redirectTo: 'dashboard',
        pathMatch: 'full'
      },
      {
        path: 'contracts',
        redirectTo: 'contracts/list',
        pathMatch: 'full'
      },
      {
        path: 'contract-templates',
        redirectTo: 'contract-templates/list',
        pathMatch: 'full'
      },
      {
        path: 'customers',
        redirectTo: 'customers/list',
        pathMatch: 'full'
      },
    ]
  },
  {
    path: 'login',
    component: LoginComponent,
    canActivate: [NonAuthGuard]
  },
  {
    path: '',
    redirectTo: 'portal/dashboard',
    pathMatch: 'full'
  },
  {
    path:'**',
    pathMatch: 'full',
    component: WrongRouteComponent
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
