export class Token {
    id: string;
    email: string;
    roles: string[] = [];
    iat: string;
    exp: string;

    constructor(obj = null) {
        obj = obj || {};
        this.id = obj.id || "";
        this.email = obj.email || "";
        this.roles = [];
        if (obj.roles && obj.roles instanceof Array) {
            obj.roles.forEach((item) => {
                this.roles.push(item);
            })
        }
        this.iat = obj.iat;
        this.exp = obj.exp;
    }
}