import { Router } from '@angular/router';
import { TokenResponse } from './../../utils/models/token-response/token-response.model';
import { Component, OnInit, OnDestroy, Renderer2 } from '@angular/core';
import { FormGroup, Validators, FormControl } from '@angular/forms';
import { AppService } from 'src/app/utils/services/app.service';
import { ToastrService } from 'ngx-toastr';
import { AuthService } from 'src/app/utils/services/auth.service';
import { TokenItem } from 'src/app/utils/models/token-response/token-item.model';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit, OnDestroy {
  public loginForm: FormGroup;

  ok = false;
  token = new TokenItem();
  isSubmit = false;

  constructor(
    private router: Router,
    private renderer: Renderer2,
    private toastr: ToastrService,
    private authService: AuthService
  ) {}

  ngOnInit() {
    this.renderer.addClass(document.body, 'login-page');
    this.loginForm = new FormGroup({
      email: new FormControl(null, [Validators.required]),
      password: new FormControl(null, Validators.required)
    });
  }

  get email() { return this.loginForm.get('email') }
  get password() { return this.loginForm.get('password') }

  login() {
    this.isSubmit = true;
    if (this.loginForm.valid) {
      this.authService.login(this.loginForm.value).subscribe((res) => {
        this.token = new TokenResponse(res).object
        this.authService.setToken(this.token.token);
      }, err => {
        console.log(err);
      },() => {
        this.authService.getUserInfo(this.authService.jwtDecode(this.authService.getToken()).id).subscribe((res) => {
          this.authService.setPartyId(res['object']['partyId']);
        }, err => {

        }, () => {
          this.loginSuccess();
        })
        
      })
    } else {
    }
  }

  Toast(i) {
    if(i){
      this.toastr.error('Hello world!', 'Toastr fun!');
    } else {
      this.toastr.success('Hello world!', 'Toastr fun!');
    }
    
  }

  ngOnDestroy() {
    this.renderer.removeClass(document.body, 'login-page');
  }

  loginSuccess() {
    this.router.navigate(['/portal']);
  }

  get errorMessage() {
    return {
      email: {
        required: 'Vui lng nhp thng tin!',
      },
      password: {
        required: 'Vui lng nhp thng tin!'
      }
    }
  }
}