export class CreateTemplateItem {
    id: string;
    name: string;
    templateFields: any[] = [];
    templateSigns: any[] = [];
    type: string;

    constructor(obj = null) {
        obj = obj || {};
        this.id = obj.id || '';
        this.name = obj.name || '';
        this.templateFields = [];
        this.templateSigns = [];
        this.type = obj.type || '';
    }
}