import { ListCustomerItem } from './../../../utils/models/customer/list-customer/list-customer-item.model';
import { ListCustomerResponse } from './../../../utils/models/customer/list-customer/list-customer-response.model';
import { CustomerService } from './../../../utils/services/customer.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-customer-list',
  templateUrl: './customer-list.component.html',
  styleUrls: ['./customer-list.component.scss']
})
export class CustomerListComponent implements OnInit {
  typeCustomer = {
    BUSINESS: 'Doanh nghip',
    CUSTOMER: 'Khch hng cc nhn'
  }

  status = {
    N: {
      style: 'badge-success-new',
      label: 'ang hot ng',
    },
    Y: {
      style: 'badge-danger-new',
      label: 'Ngng hot ng',
    },
  }

  listReponse = new ListCustomerResponse();
  listCustomer: ListCustomerItem[] = [];
  page = 1;
  maxSize = 10;
  propertiesSort = '[maSoThue]';
  sort = 'DESC';
  totalElement = 0;

  constructor(
    private customerService: CustomerService
  ) { }

  ngOnInit(): void {
    this.loadData();
  }

  loadData() {
    this.customerService.getListCustomers(this.page, this.maxSize, this.propertiesSort, this.sort).subscribe((res) => {
      this.listReponse = new ListCustomerResponse(res);
      this.listCustomer = this.listReponse.object.data;
      console.log(this.listReponse);

      this.page = this.listReponse.object.page;
      this.maxSize = this.listReponse.object.maxSize;
      this.propertiesSort = this.listReponse.object.propertiesSort;
      this.sort = this.listReponse.object.sort;
      this.totalElement = this.listReponse.object.totalElement;
    }, err => {
      console.log(err);
    }, () => {

    });
  }

  loadPage(page: number) {
    if (page !== this.page) {
      this.page = page;
      this.customerService.getListCustomers(this.page, this.maxSize, this.propertiesSort, this.sort).subscribe((res) => {
        this.listReponse = new ListCustomerResponse(res);
        this.listCustomer = this.listReponse.object.data;

        this.page = this.listReponse.object.page;
        this.maxSize = this.listReponse.object.maxSize;
        this.propertiesSort = this.listReponse.object.propertiesSort;
        this.sort = this.listReponse.object.sort;
        this.totalElement = this.listReponse.object.totalElement;
      }, err => {
        console.log(err);
      }, () => {
  
      });
    }
  }
}
