export class ListTemplateItem {
    id: string;
    name: string;
    type: string;
    dateUpload: string;
    templatePath: string;
   
    constructor(obj = null) {
        obj = obj || {};
        this.id = obj.id || '';
        this.name = obj.name || '';
        this.type = obj.type || '';
        this.dateUpload = obj.dateUpload || '';
        this.templatePath = obj.templatePath || '';
    }

}