import { ToastrService } from 'ngx-toastr';
import { CreateCustomerResponse } from '../../../../utils/models/customer/create-customer/create-customer-response.model';
import { CustomerService } from './../../../../utils/services/customer.service';
import { Router } from '@angular/router';
import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray, ReactiveFormsModule } from '@angular/forms';
import { CreeatCustomerParam } from 'src/app/utils/models/customer/create-customer/create-customer-param.model';

@Component({
  selector: 'app-new-enterprise-customer',
  templateUrl: './new-enterprise-customer.component.html',
  styleUrls: ['./new-enterprise-customer.component.scss']
})
export class NewEnterpriseCustomerComponent implements OnInit {
  @Input() type: number;

  formStaff: FormGroup;
  formCompany: FormGroup;
  isSubmit = false;
  customer: CreateCustomerResponse;

  constructor(
    private fb: FormBuilder,
    private router: Router,
    private toast: ToastrService,
    private customerService: CustomerService
  ) { }

  ngOnInit(): void {
    this.formStaff = this.fb.group({
      ten: new FormControl('Nguyen Van A', [Validators.required]),//
      chucVu: new FormControl('Nhan vien', [Validators.required]),//
      cmnd: new FormControl('131513', [Validators.required]),//
      ngayCap: new FormControl('26/03/2020', [Validators.required]),//
      noiCap: new FormControl('Quang Nam', [Validators.required]),//
      ngaySinh: new FormControl('26/02/1995', [Validators.required]),//
      gioiTinh: new FormControl('Nam', [Validators.required]),//
      soNha: new FormControl('', []),
      duong: new FormControl('', []),
      thanhPho: new FormControl('', []),
      diaChiThuongTru: new FormControl('12 Nguyen Van Linh', [Validators.required]),//
      email: new FormControl('staff@gmail.com', [Validators.required]),//
      sdt: new FormControl('0906132131', [Validators.required])//
    });

    this.formCompany = this.fb.group({
      tenToChuc: new FormControl('Nguyen Van B', [Validators.required]),//
      soDkdn: new FormControl('1011212', [Validators.required]),//
      ngayCapSoDkdn: new FormControl('26/03/2020', [Validators.required]),//
      noiCapSoDkdn: new FormControl('Quang Nam', [Validators.required]),//
      diaChiThanhToan: new FormControl('23 Phan Chau Trinh', [Validators.required]),//
      diaChiTruSo: new FormControl('12 Tran Cao Van', [Validators.required]),//
      maSoThue: new FormControl('MST', [Validators.required]),//
      soTaiKhoanGiaoDich: new FormControl('003146430', [Validators.required]),//
      nganHangGiaoDich: new FormControl('ACB', [Validators.required]),//
      sdt: new FormControl('0123012021', [Validators.required]),//
      thongTinLienHe: new FormControl(''),
      email: new FormControl('company@gmail.com', [Validators.required]),//
      taiKhoan: new FormControl('')
    })
  }

  get ten() { return this.formStaff.get('ten') }
  get chucVu() { return this.formStaff.get('chucVu') }
  get cmnd() { return this.formStaff.get('cmnd') }
  get ngayCap() { return this.formStaff.get('ngayCap') }
  get noiCap() { return this.formStaff.get('noiCap') }
  get ngaySinh() { return this.formStaff.get('ngaySinh') }
  get gioiTinh() { return this.formStaff.get('gioiTinh') }
  get soNha() { return this.formStaff.get('soNha') }
  get duong() { return this.formStaff.get('duong') }
  get thanhPho() { return this.formStaff.get('thanhPho') }
  get diaChiThuongTru() { return this.formStaff.get('diaChiThuongTru') }
  get email() { return this.formStaff.get('email') }
  get sdt() { return this.formStaff.get('sdt') }

  get tenToChuc() { return this.formCompany.get('tenToChuc') }
  get soDkdn() { return this.formCompany.get('soDkdn') }
  get ngayCapSoDkdn() { return this.formCompany.get('ngayCapSoDkdn') }
  get noiCapSoDkdn() { return this.formCompany.get('noiCapSoDkdn') }
  get diaChiThanhToan() { return this.formCompany.get('diaChiThanhToan') }
  get diaChiTruSo() { return this.formCompany.get('diaChiTruSo') }
  get maSoThue() { return this.formCompany.get('maSoThue') }
  get soTaiKhoanGiaoDich() { return this.formCompany.get('soTaiKhoanGiaoDich') }
  get nganHangGiaoDich() { return this.formCompany.get('nganHangGiaoDich') }
  get sdtToChuc() { return this.formCompany.get('sdt') }
  get thongTinLienHe() { return this.formCompany.get('thongTinLienHe') }
  get emailCongTy() { return this.formCompany.get('email') }
  get taiKhoan() { return this.formCompany.get('taiKhoan') }

  get errorMessage() {
    return {
      required: 'Vui lng nhp thng tin!'
    }
  }

  createCustomer() {
    this.isSubmit = true;
    if (this.formStaff.valid && this.formCompany.valid) {
      this.customerService.createCustomer(this.makeParam).subscribe((res) => {
        this.customer = new CreateCustomerResponse(res);
      }, err => {

      }, () => {
        if(this.customer.statusCode === '200' && this.customer.message == 'IDG-00000000') {
          this.router.navigate(['/portal/customers/list']);
        } else {
          this.Toast('error', ' c li xy ra! Khng th khi to khch hng mi!');
        }
      })
    }

  }

  get makeParam() {
    let param = {
      daiDien: this.formStaff.value,
      toChuc: this.formCompany.value
    };
  
    return new CreeatCustomerParam(param);
  }

  selectGender(index) {
    this.gioiTinh.setValue(index);
  }

  //MARK: extendsion
  inputMST(e) {
    var text = e.target.value;
    this.taiKhoan.setValue(text);
  }

  convertStringToDate(str: string) {
    var values = str.split('/');
    return `${values[2]}-${values[1]}-${values[0]}T00:00:00+07:00`;
  }

  checkValue(str, max) {
    if (str.charAt(0) !== '0' || str == '00') {
      var num = parseInt(str);
      if (isNaN(num) || num <= 0 || num > max) num = 1;
      str = num > parseInt(max.toString().charAt(0)) && num.toString().length == 1 ? '0' + num : num.toString();
    };
    return str;
  };

  input_(e: any, fc: FormControl) {
    var input = e.target.value;
    if (/\D\/$/.test(input)) input = input.substr(0, input.length - 3);
    var values = input.split('/').map(function (v) {
      return v.replace(/\D/g, '')
    });
    if (values[0]) values[0] = this.checkValue(values[0], 31);
    if (values[1]) values[1] = this.checkValue(values[1], 12);
    var output = values.map(function (v, i) {
      return v.length == 2 && i < 2 ? v + '/' : v;
    });
    e.target.value = output.join('').substr(0, 14);
    fc.setValue(e.target.value);
  }

  blur_(e: any, fc: FormControl) {
    var input = e.target.value;
    var values = input.split('/').map(function (v, i) {
      return v.replace(/\D/g, '')
    });
    var output = '';

    if (values.length == 3) {
      var year = values[2].length !== 4 ? parseInt(values[2]) + 2000 : parseInt(values[2]);
      var month = parseInt(values[1]) - 1;
      var day = parseInt(values[0]);
      var d = new Date(year, month, day);
      if (d && d != null && d != undefined) {
        var dates = [d.getDate().toString(), (d.getMonth() + 1).toString(), d.getFullYear().toString()];
        output = dates.map(function (v) {
          v = v.toString();
          return v.length == 1 ? '0' + v : v;
        }).join('/');
      };
    };
    e.target.value = output;
    fc.setValue(e.target.value);
  }

  Toast(type: string, msg: string) {
    switch (type) {
      case 'error': 
        this.toast.error(msg,'Thng bo');
        break;
      case 'success':
        this.toast.success(msg, 'Thng bo');
        break;
    }
  }
}