import { Injectable } from '@angular/core';
import { API } from './api';

const TEMPLATE_GROUP_LIST = '/template-service/templates-groups/list';
const TEMPLATE_LIST = '/template-service/templates/list';
const TEMPLATE_URL = '/template-service/templates';
const CREATE_TEMPLATE_URL = '/template-service/templates/';
const PREVIEW_URL = '/template-service/templates/preview';

@Injectable({
  providedIn: 'root'
})
export class TemplateService {

  constructor(private api: API) { }

  /**
   * Get list group template
   * @param partyId 
   * @param page 
   * @param maxSize 
   * @param propertiesSort 
   * @param sort 
   */
  getListGroupTemplate(partyId, page, maxSize, propertiesSort, sort) {
    const url = TEMPLATE_GROUP_LIST + `?partyId=${partyId}&page=${page}&maxSize=${maxSize}&propertiesSort=${propertiesSort}&sort=${sort}`;
    return this.api.get(url);
  }

  /**
   * Get list template
   * @param partyId 
   * @param page 
   * @param maxSize 
   * @param propertiesSort 
   * @param sort 
   */
  getListTemplate(partyId, page, maxSize, propertiesSort, sort) {
    const url = TEMPLATE_LIST + `?partyId=${partyId}&page=${page}&maxSize=${maxSize}&propertiesSort=${propertiesSort}&sort=${sort}`;
    return this.api.get(url);
  }

  postCreateTemplate(groupId, data) {
    console.log(data);
    const url = CREATE_TEMPLATE_URL + `?groupId=${groupId}`;
    return this.api.post(url, data);
  }

  getConfigTemplate(id) {
    const url = TEMPLATE_URL +`/${id}/all-config`;
    return this.api.get(url);
  }

  /**
   * PORT upload template
   * @param id 
   * @param formData 
   */
  postUploadTempate(id, formData: FormData) {
    console.log('File template => ', formData);
    console.log(formData.getAll);
    const url = TEMPLATE_URL + `/${id}/upload`;
    return this.api.postMultiPath(url, formData);
  }

  previewTemplate(formData: FormData) {
    return this.api.postMultiPath(PREVIEW_URL, formData);
  }
}
